import React, { useState, useEffect } from 'react'
import ComPagination from '@/components/comPagination/ComPagination.jsx'
import { Input, InputNumber, Table, Tag, Button, Modal, Form, Select, Alert } from 'antd';
import moment from 'moment';
import { debounce, showDialog } from '../../utils/util';
import './Balance.less'

import { credit, account, gateway, recharge, pay, getPayStatus, withdrawRule, withdraw, unSh } from "../../api/finance"
import { getCommon } from "../../api/common"

let timer = null;
let time = 300000;  // 5分钟
const body = document.body;
export default function Balance() {
  // 分页相关
  const [total, setTotal] = useState(200)
  const [params, setParams] = useState({
    keywords: '',
    page: 1,
    limit: 20,
    orderby: 'id',
    sort: 'desc'
  })

  useEffect(() => {
    getAccount()
    getGateWay()
    getUnBalance()
    getWithdrawRule()
  }, [])

  useEffect(() => {
    getCredit()
  }, [params]);

  // 支付接口
  const [gatewayDatas, setGatewayDatas] = useState([])
  // 三方支付的html
  const [payHtml, setPayHtml] = useState("");
  // 当前选择的支付方式
  const [curPay, setCurPay] = useState("");
  // 公共配置
  const [commonData, setCommonData] = useState()

  const [errText, setErrText] = useState("")

  // 余额
  const [balance, setBalance] = useState()
  // 待审核金额
  const [unBalance, setUnBalance] = useState()

  // 表格数据
  const [isTableLoading, setIsTableLoading] = useState(false)
  const [datasource, setDatasource] = useState([])
  const [balanceType] = useState({
    Artificial: { text: "人工" },
    Recharge: { text: "充值" },
    Applied: { text: "扣费" },
    Overpayment: { text: "超付" },
    Underpayment: { text: "少付" },
    Refund: { text: "退款" },
    Withdraw: { text: "提现" }
  })
  // 提现规则
  const [ruleData, setRuleData] = useState({})



  const changePage = (page, limit) => {
    setParams(params => {
      return { ...params, page, limit }
    })
  }
  const columns = [
    {
      title: 'ID',
      dataIndex: 'id',
      key: 'id',
      align: 'left',
      width: 80
    },
    {
      title: '金额',
      dataIndex: 'amount',
      key: 'amount',
      width: 100,
      align: 'left',
      render: (amount) => (
        <>{amount}</>
      )
    },
    {
      title: '备注',
      dataIndex: 'notes',
      key: 'notes',
      ellipsis: true,
      width: 500
    },
    {
      title: '类型',
      dataIndex: 'type',
      key: 'type',
      width: 150,
      render: (_, { type }) => (
        <>
          {/* <Tag color={balanceType[type].color}>
            {balanceType[type].text}
          </Tag> */}
          <span className={`balance-tag ${type}`}>{balanceType[type].text}</span>
        </>
      )
    },
    {
      title: '时间',
      dataIndex: 'create_time',
      key: 'create_time',
      width: 200,
      render: (create_time) => (
        <>
          {moment(create_time * 1000).format("YYYY-MM-DD HH:mm")}
        </>
      )
    }
  ]
  // 充值 modal
  const [rechargeModalVis, setRechargeModalVis] = useState(false)
  // 提现 modal
  const [withdrawalModalVis, setWithdrawalModalVis] = useState(false)

  // form相关
  const { Option } = Select;
  const [rechargeForm] = Form.useForm();
  const [withdrawalForm] = Form.useForm();
  const [formLayout, setFormLayout] = useState('horizontal');
  const [way, setWay] = useState("alipay")
  // 充值方式切换选择时
  const selectChange = (e) => {
    let isPass = true
    const formData = rechargeForm.getFieldsValue();

    if (!formData.amount) {
      isPass = false
      setErrText("请输入充值金额")
    }
    if (isPass) {
      setErrText("")
      doRecharge(formData)
    }
  };

  // 充值金额变动时
  const inputChange = (e) => {
    const formData = rechargeForm.getFieldsValue();
    let isPass = true
    if (!formData.gateway) {
      isPass = false
      setErrText("请选择充值方式")
    }

    if (!formData.amount) {
      isPass = false
    }

    if (Number(formData.amount) === 0) {
      isPass = false
    }



    if (isPass) {
      setErrText("")
      doRecharge(formData)
    }
  }

  // 充值
  const doRecharge = (params) => {
    recharge(params).then(res => {
      if (res.data.status === 200) {
        const orderId = res.data.data.id
        const gateway = params.gateway
        pay({ id: orderId, gateway }).then(res => {
          setPayHtml(res.data.data.html);
          pollingStatus(orderId)
        })
      }
    })
  }

  // 轮询支付状态
  const pollingStatus = (id) => {
    if (timer) {
      clearInterval(timer)
    }

    timer = setInterval(async () => {
      const res = await getPayStatus(id);
      time = time - 2000
      if (res.data.code === "Paid") { // 支付成功
        showDialog(res.data.msg);
        clearInterval(timer);
        setRechargeModalVis(false)
        getCredit()
        return false;
      }
      // 5分钟到点清除定时器
      if (time === 0) {
        clearInterval(timer);
        setRechargeModalVis(false)
        return false;
      }
    }, 2000)

  };

  const quitRecharge = () => {
    body.classList.remove("body-overflow-hidden");
    setRechargeModalVis(false)
    clearInterval(timer)
    time = 300000
  }

  const widthdrawalSelect = (e) => {
    setWay(e)
  }


  const formItemLayout =
    formLayout === 'horizontal'
      ? {
        labelCol: {
          span: 4,
        },
        wrapperCol: {
          span: 20,
        }
      }
      : null;


  // 获取提现规则
  const getWithdrawRule = () => {
    withdrawRule().then(res => {
      if (res.data.status == 200) {
        console.log(res.data.data);
        setRuleData(res.data.data)
      }
    })
  }
  // 申请提现 提交
  const withdrawalSub = () => {
    const formData = withdrawalForm.getFieldsValue()
    let isPass = true
    if (formData.method === "alipay") { // 支付宝提现
      if (!formData.account) {
        isPass = false
        setErrText("请输入支付宝账号")
      }
      if (!formData.amount) {
        isPass = false
        setErrText("请输入提现金额")
      }
    }


    if (formData.method === "bank") { // 银行卡提现
      if (!formData.card_number) {
        isPass = false
        setErrText("请输入银行卡号")
      }
      if (!formData.name) {
        isPass = false
        setErrText("请输入银行卡持有人姓名")
      }
      if (!formData.amount) {
        isPass = false
        setErrText("请输入提现金额")
      }
    }

    if (isPass) {
      setErrText("")
      const params = {
        source: "credit",
        method: formData.method,
        amount: formData.amount,
        card_number: formData.card_number,
        name: formData.name,
        account: formData.account,
      }
      // 提现操作
      withdraw(params).then(res => {
        if (res.data.status === 200) {
          showDialog("申请提现成功！", "success")
          // 重新调用获取待审核金额
          getUnBalance()
          // 重新获取金额
          getAccount()
          setWithdrawalModalVis(false)
          getCredit()
        }
      })
    }
  }

  // 获取账户详情
  const getAccount = () => {
    account().then(res => {
      if (res.data.status === 200) {
        const credit = res.data.data.account.credit
        setBalance(credit)
      }
    })
  }

  // 获取余额记录列表
  const getCredit = () => {
    setIsTableLoading(true)
    getCommon().then(res => {
      if (res.data.status === 200) {
        const prefix = res.data.data.currency_prefix
        const code = res.data.data.currency_code
        setCommonData(res.data.data)
        credit({ ...params }).then(res => {
          if (res.data.status === 200) {
            let count = res.data.data.count;
            setTotal(() => {
              return count
            })
            let list = res.data.data.list

            list = list.filter(item => {
              return item.type !== "Artificial"
            })

            list.map(item => {
              if (item.amount) {
                item.amount = prefix + item.amount + code
              }
            })
            setDatasource(() => {
              return list
            })
          }
          setIsTableLoading(false)
        })
      }
    })
  }

  // 充值按钮点击事件
  const rechargeHandle = () => {
    setErrText("")
    body.classList.add("body-overflow-hidden");
    rechargeForm.setFieldsValue({
      amount: null,
      gateway: gatewayDatas[0].name
    })
    setRechargeModalVis(true)
  }

  // 提现按钮点击事件
  const withdrawalHandle = () => {
    setErrText("")
    setWithdrawalModalVis(true)
    body.classList.add("body-overflow-hidden");
    withdrawalForm.setFieldsValue({
      source: "credit",
      method: ruleData.method ? ruleData.method[0] : '',
      amount: null,
      card_number: null,
      name: null,
      account: null
    })
  }

  // 获取充值方式
  const getGateWay = () => {
    gateway().then(res => {
      if (res.data.status) {
        setGatewayDatas(res.data.data.list)
      }
    })
  }

  // 获取待审核金额
  const getUnBalance = () => {
    unSh().then(res => {
      if (res.data.status === 200) {
        setUnBalance(res.data.data.amount)
      }
    })
  }

  return (
    <div id='Balance'>
      <div className=" balance-top">
        <div className="balance-top-mon">
          <div className="mon-show">
            <span className='show-text'>金额(元):</span>
            <span className='show-num'>{balance}</span>
          </div>
          <div className="mon-operation">
            <Button className='recharge-btn' size='small' onClick={rechargeHandle}>充值</Button>
            <Button className='withdrawal-btn' size='small' onClick={withdrawalHandle}>提现</Button>
          </div>
        </div>
        <div className="balance-top-unmon">
          <span className='show-text'>待退款金额(元):</span>
          <span className='show-num'>{unBalance}</span>
        </div>
      </div>
      <div className='com-card'>
        <div className="card-top">
          <p>余额记录</p>
        </div>
        <div className="card-main">
          <div className="main_table">
            <Table columns={columns}
              dataSource={datasource}
              pagination={false}
              rowKey={record => record.id}
              loading={isTableLoading}
              scroll={datasource.length !== 0 ? {
                x: '100%',
                scrollToFirstRowOnChange: true
              } : null}
            />
          </div>
        </div>
        {/* 分页模块 */}
        <ComPagination total={total} page={params} changePage={changePage}></ComPagination>
      </div>


      {/* 充值moddal */}
      <Modal
        visible={rechargeModalVis}
        footer={null}
        centered={true}
        closable={false}
        style={{ borderRadius: "12px", overflow: "auto" }}
        width="6.40rem"
        onCancel={quitRecharge}
        getContainer={false}
      >
        <div className="recharge-modal">
          <div className="modal-title">
            <div className='title-main-text'>充值</div>
          </div>
          <div className="modal-form">
            <Form
              {...formItemLayout}
              layout={formLayout}
              form={rechargeForm}
              initialvalues={{
                layout: formLayout,
              }}
              colon={false}
              size={'large'}>
              <Form.Item label="充值金额" name="amount"
                getValueFromEvent={(e) => {
                  return e ? e.toString().replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3') : e;
                }}
              >
                {/* <Input className='modal-code-input' placeholder='单笔最小金额为300元' /> */}
                <InputNumber controls={false} style={{ width: '100%' }} onChange={debounce(inputChange, 500)} className='modal-code-input' placeholder={commonData ? `单笔最小金额为${commonData.recharge_min}${commonData.currency_code}` : null} />
              </Form.Item>
              <Form.Item label="充值方式" name="gateway"
              >
                <Select size='large' onChange={selectChange} style={{ width: '100%' }}>
                  {
                    gatewayDatas ?
                      gatewayDatas.map(item => {
                        return <Option key={item.id} value={item.name}>{item.title}</Option>
                      })
                      :
                      null
                  }
                </Select>
              </Form.Item>
              <Form.Item noStyle>
                {
                  errText ?
                    <Alert message={errText} type="error" showIcon />
                    :
                    null
                }

              </Form.Item>
              <Form.Item noStyle>
                <div className='pay-list'>
                  {/* <div dangeroussetinnerhtml={{ __html: payHtml }}></div> */}
                  <div dangerouslySetInnerHTML={{ __html: payHtml }}></div>
                </div>
              </Form.Item>
            </Form>
          </div>
        </div>
      </Modal>
      {/* 提现modal */}
      <Modal
        visible={withdrawalModalVis}
        footer={null}
        centered={true}
        closable={false}
        style={{ borderRadius: "12px", overflow: "auto" }}
        width="640px"
        onCancel={() => {
          setWithdrawalModalVis(false)
          body.classList.remove("body-overflow-hidden");
        }}
        getContainer={false}
      >
        <div className="withdrawal-modal">
          <div className="modal-title">
            申请提现
          </div>
          <div className="modal-form">
            <Form
              {...formItemLayout}
              layout={formLayout}
              form={withdrawalForm}
              initialvalues={{
                layout: formLayout,
              }}
              colon={false}
              size={'large'}>
              <Form.Item label="提现方式" name="method">
                <Select size='large' onChange={widthdrawalSelect} style={{ width: '100%' }}>
                  {
                    ruleData.method && ruleData.method.map(item => {
                      return <Option key={item} value={item}>{item == 'bank' ? '银行卡' : '支付宝'}</Option>
                    })
                  }
                  {/* <Option key={1} value="alipay">支付宝</Option>
                  <Option key={2} value="bank">银行卡</Option> */}
                </Select>
              </Form.Item>
              {
                way === "alipay" ?
                  <Form.Item label="支付宝账号" name="account">
                    <Input className='modal-code-input' />
                  </Form.Item>
                  :
                  <>
                    <Form.Item label="银行卡号" name="card_number">
                      <Input className='modal-code-input' />
                    </Form.Item>
                    <Form.Item label="姓名" name="name">
                      <Input className='modal-code-input' />
                    </Form.Item>
                  </>
              }
              <Form.Item label="提现金额" name="amount">
                <Input className='modal-code-input'
                  placeholder={`可提取${balance}元`}
                  suffix={
                    <Button type="text" size='small' className='r-button' onClick={() => withdrawalForm.setFieldsValue({ amount: balance })} >全部</Button>
                  }
                />
              </Form.Item>
              <Form.Item noStyle>
                {
                  errText ?
                    <Alert message={errText} type="error" showIcon />
                    :
                    null
                }
              </Form.Item>
              <Form.Item noStyle>
                <div className="modal-footer">
                  <Button className='form-button sub' onClick={withdrawalSub} type="primary">提交</Button>
                  <Button className='form-button close' onClick={() => {
                    setWithdrawalModalVis(false)
                    body.classList.remove("body-overflow-hidden");
                  }} type="primary">取消</Button>
                </div>
              </Form.Item>
            </Form>
          </div>
        </div>
      </Modal>

    </div>
  )
}
